body {
    font-family: sans-serif;
}

* {
    box-sizing: border-box;
}

h1 {
    font-weight: 100;
    margin-bottom: 0;
    color: purple;
}

.main-header {
    /* margin: 0 20px; */
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
    display: flex;
}

.main-header .toolbar {
    text-align: right;
    flex-grow: 1;
    margin-top: 25px
}

.main-header .toolbar .button {

    font-size: 1.5em;
    color: grey;
    cursor: pointer;
    -webkit-transition: color 300ms ease-in;
    transition: color 300ms ease-in;
}


.main-header .toolbar .button:hover {
    color: black;
}


.slice {
    border: 1px solid #d8d8d8;
    border-radius: 2px;
    min-height: 100px;
    box-shadow: 1px 2px 7px rgba(0, 0, 0, 0.17);
    -webkit-transition: opacity 200ms ease-in;
    transition: opacity 200ms ease-in;
    margin-right: auto;
    margin-left: auto;
    min-width: 200px;
    height: 100%;

    display: flex;
    flex-direction: column;
}

.slice.dragging {
    opacity: 0.3;
}

.slice .body {
    padding: 10px;
    overflow: hidden;
    flex-grow: 1;
}

.slice .header {
    padding: 5px 10px;
    /* color: white; */
    background: #efefef;
    border-bottom: 1px solid #d8d8d8;
    cursor: pointer;

    display: flex;

}


.slice .header .button {
    border-radius: 3px;
    font-size: 17px;
    margin: 0 3px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    right: -6px;
    padding: 2px 5px;
    color: gray;
}

.slice .header .button-toggle {
    font-size: 11px;
    margin-top: 5px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    color: gray;
    margin-right: 5px;
    -webkit-user-select: none;
}

.slice .header .button-toggle.active {
    color: purple;
}

.slice .header .button:hover {
  color: black;
}

.slice .header .button.active {
    color: purple;
}

.slice .header .title {
    font-size: medium;
    width: 100%;
    border: none;
    background: transparent;
    outline: none;
}

.slice .header ::-webkit-input-placeholder { font-style: italic; }




.slice .footer {
    padding: 5px 10px;
    background: #f5f5f5;
    border-bottom: 1px solid #d8d8d8;
}



.time-slider {
    width: 100%;
    position: relative;
    left: -2px;
}

.widget {
    flex-grow: 1;
}

.slice.reference {
    box-shadow: 1px 2px 7px rgba(94, 8, 148, 0.27);
}

.slice.reference .header {
    background: #f2eaff
}


svg.measure {
	position: absolute;
	left: -1000px;
	top: -1000px;
}


circle.active {
	fill: white;
}



rect.inactive {
	fill: #bbb;
}

rect.active {
	fill: red;
}

.genesis {
	fill: black;
}


text {
    alignment-baseline: central;
    fill: white;
    font-size: small;
    pointer-events: none;
    -webkit-user-select: none;
}


line.mainline {
	stroke: black;
	stroke-width: 2;
}

line.inactive {
	stroke: #bbb;
	stroke-width: 1;
}


path.mainline {
	stroke: black;
	stroke-width: 2;
	fill: none;
}

path.inactive {
	stroke: #bbb;
	stroke-width: 1;
	fill: none;
}

svg.dag {
	width: 100%;
}

.row-1 {
  max-width: 70vw;
  margin-left: auto;
  margin-right: auto;
}


.row > * {
	display: flex;
}
.row {
  border-bottom: 5px solid transparent;
}
.row:first-child {
	border-top: 5px solid transparent;
}


.row.insert-top {
  border-top: 5px solid purple; 
}

.row.insert-bottom {
  border-bottom: 5px solid purple; 
}


.col {
	flex-basis: 0;
	flex-grow: 1;
	border-right: 5px solid transparent;
	padding: 5px;
  overflow: hidden;
}
.col:first-child {
	border-left: 5px solid transparent;
}


.col.insert-left {
  border-left: 5px solid purple; 
}

.col.insert-right {
  border-right: 5px solid purple; 
}


.fake-slice {
  border: 2px dashed #d8d8d8;
  border-radius: 2px;
  min-height: 30px;
  padding-bottom: 20px;
  text-align: center;
  font-size: 100px;
  color: #d8d8d8;
  -webkit-user-select: none;
  cursor: pointer;

  -webkit-transition: all 200ms ease-in;

  transition: all 200ms ease-in;
}

.fake-slice:hover {
  border: 2px dashed gray;
  color: gray;
}


.example-enter {
  /* 0 does not work so we have to use a small number */
  /* Start our small */
  flex: .00001 1;

  -webkit-animation: flexGrow 300ms ease forwards;
  animation: flexGrow 300ms ease forwards;
}


.example-leave {
  flex: 1 1;

  -webkit-animation: flexShrink 300ms ease forwards;
  animation: flexShrink 300ms ease forwards;
}


@-webkit-keyframes flexGrow {
  to { flex: 1 1; }
}
@keyframes flexGrow {
  to { flex: 1 1; }
}

@-webkit-keyframes flexShrink {
  to {
    flex: .01 1;
    flex: .00001 1;
  }
}
@keyframes flexShrink {
  to {
    flex: .01 1;
    flex: .00001 1;
  }
}


.CodeMirror {
    height: auto !important;
    min-height: 100px;
    font-size: 13px;
}

.editor .CodeMirror pre {
    padding: 0 10px;
}

.editor .CodeMirror-lines {
    padding: 10px 0;
}


.deleted {
    color: #b30000;
    background: #fadad7;
}

.inserted {
    background: #eaf2c2;
    color: #406619;
}

.line-inserted {
    background: #c9f1c9;
}

.line-changed {
    background: #ffffbb;
}


.line-removed {
    background: #ffd7d7;
    padding: 0 10px;
}


span.result {
    background: #dedede;
    padding: 0 4px;
    margin: 0 5px;
    border-radius: 2px;
}

